const parser = new DOMParser();

/**
 * –í–æ–∑–≤—Ä–∞—â–∞–µ—Ç —Å–ª—É—á–∞–π–Ω–æ–µ —Ü–µ–ª–æ–µ —á–∏—Å–ª–æ –∏–∑ –∑–∞–¥–∞–Ω–Ω–æ–≥–æ –¥–∏–∞–ø–∞–∑–æ–Ω–∞
 *
 * @param {Number} min - –º–∏–Ω–∏–º–∞–ª—å–Ω–æ –≤–æ–∑–º–æ–∂–Ω–æ–µ –∑–Ω–∞—á–µ–Ω–∏–µ
 * @param {Number} max - –º–∞–∫—Å–∏–º–∞–ª—å–Ω–æ –≤–æ–∑–º–æ–∂–Ω–æ–µ –∑–Ω–∞—á–µ–Ω–∏–µ
 * @return {Number}
 */
const getRandomInt = (min, max) => Math.floor(Math.random() * Math.floor((max - min) + 1) + min);

/**
 * –ü–µ—Ä–µ–æ–±—Ä–∞–∑–æ–≤—ã–≤–∞–µ—Ç HTML –∫–æ–¥ —ç–ª–µ–º–µ–Ω—Ç–∞ –≤ DOM —É–∑–ª—ã
 *
 * @param {Sring} html - —Å—Ç—Ä–æ–∫–∞ —Å–æ–¥–µ—Ä–∂–∞—â–∞—è HTML
 * @param {Node} container - DOM-—ç–ª–µ–º–µ–Ω—Ç –¥–ª—è –æ—Ç—Ä–∏—Å–æ–≤–∫–∏
 */
const insertHtmlToElement = (html, container) => {
  const element = parser.parseFromString(html, `text/html`);
  const cardChildren = element.body.childNodes;
  cardChildren.forEach((childNode) => container.appendChild(childNode));
};

/**
 * –î–æ–±–∞–≤–ª—è–µ—Ç –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü—É —Å–æ–∑–¥–∞–Ω—ã–µ –∏–∑ —à–∞–±–ª–æ–Ω–∞ —ç–ª–µ–º–µ–Ω—Ç—ã
 *
 * @param {Node} container - DOM-—ç–ª–µ–º–µ–Ω—Ç –¥–ª—è –æ—Ç—Ä–∏—Å–æ–≤–∫–∏
 * @param {Array} elements - —Å–ø–∏—Å–æ–∫ —ç–ª–µ–º–µ–Ω—Ç–æ–≤ —Å –ø–∞—Ä–∞–º–µ—Ç—Ä–∞–º–∏
 * @param {Function} createElementHtml - —Ñ—É–Ω–∫—Ü–∏—è –∏–∑–º–µ–Ω–µ–Ω–∏—è —à–∞–±–ª–æ–Ω–∞ —ç–ª–µ–º–µ–Ω—Ç–∞ –Ω–∞ –æ—Å–Ω–æ–≤–µ –ø–∞—Ä–∞–º–µ—Ç—Ä–æ–≤
 */
const renderElements = (container, elements, createElementHtml) => {
  const fragment = document.createDocumentFragment();
  const elementsHtml = elements.map(createElementHtml);
  elementsHtml.forEach((element) => {
    insertHtmlToElement(element, fragment);
  });
  container.appendChild(fragment);
};

/**
 * –í–æ–∑–≤—Ä–∞—â–∞–µ—Ç —Å–ª—É—á–∞–Ω—ã–µ —Å–æ–±—ã—Ç–∏—è –º–∞—Ä—à—Ä—É—Ç–∞ –∑–∞–¥–∞–Ω–Ω–æ–≥–æ –∫–æ–ª–∏—á–µ—Å—Ç–≤–∞
 *
 * @param {Number} quantity - –Ω–æ–≤–æ–µ –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ
 * @param {Array} arrayTripPoint - –∏—Å—Ö–æ–¥–Ω—ã–π –º–∞—Å—Å–∏–≤ —Å–æ–±—ã—Ç–∏–π
 * @return {Array} –Ω–æ–≤—ã–π –º–∞—Å—Å–∏–≤ —Å–æ–±—ã—Ç–∏–π
 */
const getNewListTripPoints = (quantity, arrayTripPoint) => {
  const newArrayTripPoint = [];
  for (let i = 0; i < quantity; i++) {
    const j = getRandomInt(0, arrayTripPoint.length - 1);
    newArrayTripPoint.push(arrayTripPoint[j]);
  }
  return newArrayTripPoint;
};

export {getRandomInt, renderElements, getNewListTripPoints};
